<template>
    <div class="barCharts">
        <div ref="barCharts" class="bar_bda"></div>
        <el-dialog
            title="提示"
            :visible.sync="barTableDialogVisible"
            width="70%">
<!--            <bar-table ref="bartable"></bar-table>-->
            <div class="barTable" v-loading="barTableLoading">
                <el-table
                    :data="tableData"
                    height="250"
                    border
                    stripe
                    style="width: 100%">
                    <el-table-column
                        prop="ywlb"
                        label="业务类型">
                    </el-table-column>
                    <el-table-column
                        prop="qd"
                        label="渠道">
                    </el-table-column>
                    <el-table-column
                        prop="ywsqsj"
                        label="业务申请时间">
                    </el-table-column>
                    <el-table-column
                        prop="gdj"
                        label="供电局">
                    </el-table-column>
                    <el-table-column
                        prop="blgds"
                        label="办理工单数">
                    </el-table-column>
<!--                    <el-table-column-->
<!--                        prop="zb"-->
<!--                        label="占比">-->
<!--                    </el-table-column>-->
                </el-table>
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pages.pageNo"
                        :page-sizes="[8, 10, 20, 50]"
                        :page-size="pages.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pages.total">
                    </el-pagination>
                </div>
            </div>
            <span slot="title">
                <span class="table_title">业务办理情况明细</span>
                <span class="label">业务类别：</span>
                <el-select
                    style="width: 160px;margin-right: 10px"
                    size="mini"
                    v-model="typeValue"
                    multiple
                    filterable
                    collapse-tags
                    allow-create
                    default-first-option
                    placeholder="请选择业务类别">
                    <el-option
                        v-for="item in typeVal"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                <span class="label">渠道：</span>
                    <el-select v-model="qdValue"
                     clearable
                     placeholder="请选择渠道"
                     style="width: 140px;margin-bottom: 10px"
                     size="mini">
                        <el-option
                            v-for="item in qdOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                <span class="label">时间：</span>
                <el-date-picker
                    size="mini"
                    value-format="yyyy-MM"
                    v-model="barrDate"
                    type="monthrange"
                    align="right"
                    unlink-panels
                    range-separator="至"
                    start-placeholder="开始月份"
                    end-placeholder="结束月份">
                 </el-date-picker>
                <el-button type="primary" size="mini" style="margin: 0px 10px" @click="getBusinessSituationDetail">查询</el-button>
                <el-button type="primary" size="mini" @click="exportExcel" v-loading="dfLoadig">导出Excel</el-button>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="barTableDialogVisible = false" size="mini">取 消</el-button>
                <el-button type="primary" @click="barTableDialogVisible = false" size="mini">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
  // import barTable from "./barTable";
  import Interface from '@/base/mixin/channel-preference';
  import {CP_PATH} from '@/config'
  import axios from "axios";

  let option = {
    color: ['#3398DB'],
    tooltip: {
      trigger: 'axis',
      axisPointer: {            // 坐标轴指示器，坐标轴触发有效
        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
      }
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '8%',
      containLabel: true
    },
    xAxis: [
      {
        type: 'category',
        data: [],
        axisTick: {
          alignWithLabel: true
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisTick: {
          show: false  //坐标轴刻度线
        },
        axisLine: { //轴线
          show: false
        },
      }
    ],
    series: [
      {
        name: '数值',
        type: 'bar',
        barMaxWidth: '60',
        data: []
      }
    ]
  }
  export default {
    name: "barCharts",
    mixins: [Interface],
    components: {
      // barTable,
    },
    props: {
      typeVal:{
        type:Array,
        default:function () {
          return []
        }
      }
    },
    data() {
      return {
        barOption: option,
        barTableDialogVisible: false,
        typeOptions: [],
        typeValue: ['全部'],
        barrDate: [],
        qdOptions: [],
        qdValue: '',
        tableData:[],
        pages:{
          pageNo:1,
          pageSize:10,
          total:0,
        },
        barTableLoading:false,
        dfLoadig:false,
      }
    },
    mounted() {
      // this.init()

    },
    methods: {
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.getBusinessSituationDetail()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.getBusinessSituationDetail()
        console.log(`当前页: ${val}`);
      },
      init(dataX=[],series=[],ywType=[],date=new date()) {
        this.barrDate = date
        this.typeValue = ywType
        this.barOption.xAxis[0].data = dataX
        this.barOption.series[0].data = series
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.barCharts)
        this.mychart.setOption(this.barOption);
        let mychart = echarts.init(this.$refs.barCharts, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

        //点击事件
        let _this = this
        this.mychart.off('click');
        this.mychart.on('click', function (params) {
          _this.barTableDialogVisible = true
          //获取业务渠道下拉
          _this.GetBusinessChannel(params)
        })
      },
      //获取业务渠道下拉
      GetBusinessChannel(params){
        this.qdValue = params.name
        this.getBusinessChannel({

        }).then(res => {
          this.qdOptions = res
          this.qdOptions.unshift({
            value:'全部',
            label:'全部',
          })
          this.getBusinessSituationDetail()//进来第一次查询
        })
      },

      //获取业务渠道下拉
      getBusinessSituationDetail(){
        this.barTableLoading = true;
        (this.barrDate===undefined||this.barrDate===null)?this.barrDate = []:this.barrDate;
        this.businessSituationDetail({
          businessType:this.typeValue,//业务类型
          channel:this.qdValue,  //渠道
          current:this.pages.pageNo,
          size:this.pages.pageSize,
          startTime:this.barrDate[0]||'',
          endTime:this.barrDate[1]||'',
        }).then(res => {
          this.tableData = res.records
          this.pages.total = res.total
          this.barTableLoading = false;
          window.console.log(res)
        })
      },
      //导出excel
      exportExcel(){
        (this.barrDate===undefined||this.barrDate===null)?this.barrDate = []:this.barrDate;
        let startTime = ''
        let endTime = ''
        if (this.barrDate.length>0){
          startTime = this.barrDate[0]
          endTime = this.barrDate[1]
        }

        this.dfLoadig = true
        window.console.log(this.typeValue,'999')
        // const obj = {
        //   businessType: this.typeValue,
        //   channel: this.qdValue,
        //   startTime: startTime,
        //   endTime: endTime,
        // }
        let path = '/internetChannelController/exportBusinessDetail'+'?businessType='+this.typeValue+'&channel='+this.qdValue+'&startTime='+ startTime +'&endTime='+endTime
        axios({
          method: "get",
          url: CP_PATH + path,
          // params:obj,
          traditional: true,//这里设为true就可以了
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          responseType: 'blob'
        }).then(res => {
          const content = res.data
          const blob = new Blob([content])
          const fileName = '业务办理明细信息.xls'
          if ('download' in document.createElement('a')) { // 非IE下载
            const elink = document.createElement('a')
            elink.download = fileName
            elink.style.display = 'none'
            elink.href = URL.createObjectURL(blob)
            document.body.appendChild(elink)
            elink.click()
            URL.revokeObjectURL(elink.href) // 释放URL 对象
            document.body.removeChild(elink)
          } else { // IE10+下载
            navigator.msSaveBlob(blob, fileName)
          }
        }).finally(()=>{
          this.dfLoadig = false
        }).catch(()=>{
          this.dfLoadig = false
        })

        // window.location.href= CP_PATH +'/internetChannelController/exportBusinessDetail'+
        //   '?businessType='+this.typeValue+'&channel='+this.qdValue+'&startTime='+ startTime +'&endTime='+endTime
      }
    }
  }
</script>

<style scoped lang="scss">
    .barCharts {
        height: 100%;

        .bar_bda {
            height: 100%;
            min-height: 100px;
        }

        .table_title {
            font-weight: bold;
        }

        .label {
            margin: 0px 10px 0px 20px;
        }
        .barTable{
            padding: 0px 10px;
            ::v-deep .el-table{
                .cell{
                    padding: 3px 8px;
                }
            }
        }
    }
</style>