<template>
    <div class="acrossBar">
        <div ref="acrossBar" class="acrossBar_chart"></div>
        <el-dialog
            title="提示"
            :visible.sync="lineDialogVisible"
            width="88%">
            <div class="line-charts" v-loading="lineChartsLoading">
            </div>
            <span slot="title">
                <span class="table_title">业务办理明细<br><span style="font-weight: normal;font-size: 12px;color: #ff8000">业务类别、业务子类、单位只能有一个多选</span></span>
                <span class="label">业务类别：</span>
                <el-select
                    :multiple-limit="gnflLimit"
                    @change="handleGnfl"
                    style="width: 160px;margin-right: 10px"
                    size="mini"
                    v-model="gnflValue"
                    multiple
                    filterable
                    collapse-tags
                    allow-create
                    default-first-option
                    placeholder="请选择业务类别">
                    <el-option
                        v-for="item in gnflList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                 <span class="label">业务子类：</span>
                <el-select
                    :multiple-limit="gnxLimit"
                    @change="handleGnx"
                    style="width: 160px;margin-right: 10px"
                    size="mini"
                    v-model="gnxValue"
                    multiple
                    filterable
                    collapse-tags
                    allow-create
                    default-first-option
                    placeholder="请选择业务子类">
                    <el-option
                        v-for="item in gnxList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                <span class="label">单位：</span>
                    <el-select
                        style="width: 160px;margin-right: 10px"
                        size="mini"
                        @change="handleQd"
                        :multiple-limit="qdLimit"
                        v-model="qdValue"
                        multiple
                        filterable
                        collapse-tags
                        allow-create
                        default-first-option
                        placeholder="请选择单位">
                    <el-option
                        v-for="item in qdOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                <span> 时间：</span>
                <el-radio-group v-model="hyfbWd">
                    <el-radio :label="'年'">年</el-radio>
                    <el-radio :label="'月'">月</el-radio>
                    <el-radio :label="'日'">日</el-radio>
                </el-radio-group>
                <el-date-picker
                    v-model="hyfbDate"
                    :type="hyfbWd==='年'?'year':(hyfbWd==='月'?'month':'date')"
                    style="width: 130px"
                    size="mini"
                    placeholder="选择日期"
                    :value-format="hyfbWd==='年'?'yyyy':(hyfbWd==='月'?'yyyy-MM':'yyyy-MM-dd')">>
                </el-date-picker>
                <el-button type="primary" size="mini" style="margin: 0px 10px"
                           @click="getFunctionalActiveDistributionDetail">查询</el-button>
                <el-button type="primary" size="mini" @click="exportExcel">导出Excel</el-button>
            </span>
            <div style="text-align: center">
                <el-radio-group v-model="lineType" @change="getFunctionalActiveDistributionDetail">
                    <el-radio :label="'1'">分线</el-radio>
                    <el-radio :label="'2'">合线</el-radio>
                </el-radio-group>
            </div>
            <div style="height: 260px;margin: 10px 8px" ref="gnfnLineChart"></div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="lineDialogVisible = false" size="mini">关闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
  import {CP_PATH} from '@/config'
  import Interface from '@/base/mixin/channel-preference';
    let option ={
      dataset: {
        source: [
          ['score', 'amount', 'product'],
          [89.3, 58212, 'Matcha'],
          [57.1, 78254, 'Milk'],
          [74.4, 41032, 'Cheese'],
          [89.7, 20145, 'Matcha'],
          [68.1, 79146, 'Tea'],
          [19.6, 91852, 'Orange'],
          [10.6, 101852, 'Lemon'],
          [32.7, 20112, 'Walnut']
        ]
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
          type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
        }
      },
      grid: {
        containLabel: true,
        top : 0,    //距离容器上边界40像素
        bottom: 10,   //距离容器下边界30像素
        left:10,
        right:30,
      },
      xAxis: {
        name: ' ',
        axisTick: {
          show: true  //坐标轴刻度线
        },
        axisLine: { //轴线
          show: false
        },
        splitLine:{ //网格线
          show:true
        },
        axisLabel: { //坐标轴样式
          textStyle: {
            color: '#636363'
          }
        }},
      yAxis: {
        type: 'category',
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          textStyle: {
            color: '#636363'
          }
        }},
      series: [
        {
          type: 'bar',
          showBackground: true,
          backgroundStyle: {
            color: '#e7fcff'
          },
          encode: {
            // Map the "amount" column to X axis.
            x: 'score',
            // Map the "product" column to Y axis
            y: 'product'
          },
          barMaxWidth : 32,//柱图宽度
          itemStyle: {
            normal: {
              //这里是重点
              color: function(params) {
                //注意，如果颜色太少的话，后面颜色不会自动循环，最好多定义几个颜色
                var colorList = ['#264bcd','#00cbcb', '#61a0a8', '#d48265', '#91c7ae','#749f83', '#ca8622'];
                return colorList[params.dataIndex]
              }
            }
          }
        }
      ]
    }
  export default {
    name: "acrossBar",
    mixins: [Interface],
    components:{

    },
    props:{

    },
    data(){
      return {
        lineType:'1', //1==分线，2===和线
        barOption:option,
        //弹窗
        lineChartsLoading: false,
        lineDialogVisible: false,
        gnflValue: [],
        gnflList: [], //业务类别
        gnxValue: [],
        gnxList: [], //业务子类
        qdValue: [], //单位
        qdOptions: [],
        hyfbWd: '月',
        hyfbDate: '2021-2-1',

        lineOption: {
          grid: {
            left: '3%',
            right: '4%',
            bottom: '10',
            top:'10',
            containLabel: true
          },
          tooltip: {
            trigger: 'axis'
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            name:'',
            color: '#3c86e0',
            data: [],
            type: 'line',
          }]
        },

        gnflLimit: 20,//多选限制条数
        gnxLimit: 20,
        qdLimit: 20,
      }
    },
    mounted() {
    },
    methods:{
      //多选下拉限制条数
      handleGnfl(){ //功能分类
        this.gnxValue = []
        this.GetFeature()
        if (this.gnflValue.length>1){
          this.gnxLimit = 1
          this.qdLimit = 1
        }else if (this.gnxValue.length>1){
          this.gnflLimit = 1
          this.qdLimit = 1
        }else if(this.qdValue.length>1){
          this.gnxLimit = 1
          this.gnflLimit = 1
        }else {
          this.gnflLimit = 20;//多选限制条数
          this.gnxLimit = 20;//多选限制条数
          this.qdLimit = 20;//多选限制条数
        }
      },
      handleGnx(){  //功能项
        if (this.gnflValue.length>1){
          this.gnxLimit = 1
          this.qdLimit = 1
        }else if (this.gnxValue.length>1){
          this.gnflLimit = 1
          this.qdLimit = 1
        }else if(this.qdValue.length>1){
          this.gnxLimit = 1
          this.gnflLimit = 1
        }else {
          this.gnflLimit = 20;//多选限制条数
          this.gnxLimit = 20;//多选限制条数
          this.qdLimit = 20;//多选限制条数
        }
      },
      handleQd(){  //功能项
        if (this.gnflValue.length>1){
          this.gnxLimit = 1
          this.qdLimit = 1
        }else if (this.gnxValue.length>1){
          this.gnflLimit = 1
          this.qdLimit = 1
        }else if(this.qdValue.length>1){
          this.gnxLimit = 1
          this.gnflLimit = 1
        }else {
          this.gnflLimit = 20;//多选限制条数
          this.gnxLimit = 20;//多选限制条数
          this.qdLimit = 20;//多选限制条数
        }
      },
      //获取业务类别
      GetFunctionDistribution(params, options, pieValue) {
        this.getBusinessType({}).then(res => {
          this.gnflList = res
        }).finally(()=>{
        })
      },
      //根据功能分类获取功能项下拉框数据
      GetFeature() {
        this.getBusinessSubclass({
          businessType:this.gnflValue
        }).then(res => {
          this.gnxList = res
        })
      },
      //下拉选择
      handleGnf(a, b) {
        window.console.log(a)
        window.console.log(b)

      },
      //查询
      getFunctionalActiveDistributionDetail() {
        this.lineChartsLoading = true
        this.getTopTenDetail({
          "cities": this.qdValue,
          "dimension": this.hyfbWd,
          "time": this.hyfbDate,
          "businessType": this.gnflValue,
          "businessSubclass": this.gnxValue,
          "type":this.lineType
        }).then(res => {
          if (this.lineType==='1'){
            this.init2(res)
          }else {
            this.init2({'合线':res})
          }
        })

        this.lineChartsLoading = false
      },
      //导出excel
      exportExcel() {
        window.location.href = CP_PATH + '/internetChannelController/exportTopTenDetail' +
          '?cities=' + this.qdValue +'&dimension=' + this.hyfbWd + '&businessSubclass=' + this.gnxValue  + '&businessType=' + this.gnflValue + '&time=' + this.hyfbDate + '&type=' +this.lineType;
      },
      //弹窗折线图
      init2(data) {
        this.lineOption.xAxis.data = [];
        this.lineOption.series = [];
        let series = []
        for(let key in data){
          series.push({
            yD: data[key],
            type: 'line',
            name: key,
            smooth: true
          })
        }
        let colorArray = ['#3c86e0','#e0c53c','#14e5cd','#c96f20','#68b45b','#722dc1','#87dbc7','#0812d2']
        for (let i =0;i<series.length;i++){
          series[i].data = []
          for (let j=0;j<series[i].yD.length;j++){
            series[i].color = colorArray[i]
            series[i].data.push(series[i].yD[j].num)
            if (i===0){
              this.lineOption.xAxis.data.push(series[i].yD[j].resultDate)
            }
          }
        }
        this.lineOption.series = series
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.gnfnLineChart)
        this.mychart.setOption(this.lineOption,true);
        let mychart = echarts.init(this.$refs.gnfnLineChart, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

        // this.lineOption.xAxis.data = [];
        // this.lineOption.series[0].data = [];
        // data.forEach(item => {
        //   this.lineOption.xAxis.data.push(item.resultDate)
        //   this.lineOption.series[0].data.push(item.num)
        // })
        // let echarts = require('echarts');
        // this.mychart = echarts.init(this.$refs.gnfnLineChart)
        // this.mychart.setOption(this.lineOption,true);
        // let mychart = echarts.init(this.$refs.gnfnLineChart, true)  //监听窗口变化重新加载
        // window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载
      },
      init(dataset=[],pieOptions = [],unitValue =[],ywblWd = '',ywblDate = new Date()){ //图表数据、单位list、单位、维度、日期
        if (dataset){
          this.barOption.dataset.source = dataset
        }
        this.qdValue = unitValue;
        this.hyfbWd = ywblWd;
        this.hyfbDate = ywblDate;
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.acrossBar)
        this.mychart.setOption(this.barOption);
        let mychart = echarts.init(this.$refs.acrossBar, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载
        this.qdOptions = pieOptions
        //点击事件
        let _this = this
        this.mychart.off('click');
        this.mychart.on('click', function (params) {
          _this.gnflValue = [params.name]
          window.console.log(params)
          _this.lineDialogVisible = true
          _this.GetFunctionDistribution(params)
          _this.GetFeature()
          _this.getFunctionalActiveDistributionDetail()
        })
      }
    }
  }
</script>

<style scoped lang="scss">
    .acrossBar{
        height: 100%;
        .acrossBar_chart{
            height: 100%;
            width: 100%;
        }
    }
</style>