<template>
  <div class="analysis">
    <el-row :gutter="10">
      <el-col :span="13">
        <div class="content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>互联网渠道注册数分布</span>
          </div>
          <div class="chart-main">
            <map-temp style="width: calc(100% - 240px)" v-loading="mapLoading" ref="mapTemp"></map-temp>
            <div class="mapLabel">
              <div>
                <span>渠道：</span>
                <el-select
                  v-model="value"
                  clearable
                  placeholder="请选择"
                  style="width: calc(100% - 60px);margin-bottom: 10px"
                  size="mini"
                  @change="areaTypeChange"
                >
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </div>
              <div>
                <span>总客户数：</span>
                <span class="num">{{objTotal.customerNum}}</span>
              </div>
              <div v-show="value!=='微信'&&value!=='支付宝'">
                <span>总注册数：</span>
                <span class="num">{{objTotal.registerNum}}</span>
              </div>
              <div v-show="value==='微信'||value==='支付宝'||value==='全部'">
                <span>总绑定数：</span>
                <span class="num">{{objTotal.bindNum}}</span>
              </div>
              <div class="card_map">
                <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card">
                  <el-tab-pane label="总注册数" name="first">
                    <template v-if="registerNumArray.length>0">
                      <div class="tab_box" v-for="(item,index) in registerNumArray" :key="index">
                        <span v-if="item.name!='深圳市'&&item.name!='广州市'">{{item.name}}</span>
                        <span v-if="item.name!='深圳市'&&item.name!='广州市'">{{item.registerNum}}</span>
                      </div>
                    </template>
                    <div v-else style="text-align: center;color: #009fec;margin-top: 20px">暂无数据</div>
                  </el-tab-pane>
                  <el-tab-pane label="总绑定数" name="second">
                    <template v-if="bindNumArray.length>0">
                      <div class="tab_box" v-for="(item,index) in bindNumArray" :key="index">
                        <span v-if="item.name!='深圳市'&&item.name!='广州市'">{{item.name}}</span>
                        <span v-if="item.name!='深圳市'&&item.name!='广州市'">{{item.bindNum}}</span>
                      </div>
                    </template>
                    <div v-else style="text-align: center;color: #009fec;margin-top: 20px">暂无数据</div>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </div>
          </div>
        </div>
        <div class="content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>渠道功能分类分布</span>
            <div class="line_nav">
              <span>渠道：</span>
              <el-select
                style="width: 220px;margin-right: 10px"
                size="mini"
                v-model="lineValue"
                multiple
                filterable
                collapse-tags
                allow-create
                default-first-option
                placeholder="请选择文章标签"
              >
                <el-option
                  v-for="item in lineOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span>时间：</span>
              <el-date-picker
                size="mini"
                value-format="yyyy-MM"
                v-model="lineDate"
                type="monthrange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始月份"
                end-placeholder="结束月份"
              ></el-date-picker>
              <el-button
                size="mini"
                type="primary"
                style="margin-left: 10px"
                @click="getFunctionDistribution"
              >查找</el-button>
            </div>
          </div>
          <div class="chart-main" style="height: 430px" v-loading="lineLoading">
            <line-charts ref="lineCharts"></line-charts>
          </div>
        </div>
      </el-col>
      <el-col :span="11">
        <div class="content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>互联网渠道办理业务TOP10排名</span>
            <div class="line_nav yw_nav">
              <span>单位：</span>
              <el-select
                style="width: 220px;margin-right: 10px"
                size="mini"
                v-model="unitValue"
                multiple
                filterable
                collapse-tags
                allow-create
                default-first-option
                placeholder="请选择"
              >
                <el-option
                  v-for="item in unitOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span>时间：</span>
              <el-radio-group v-model="ywblWd" @change="dateChange">
                <el-radio :label="'年'">年</el-radio>
                <el-radio :label="'月'">月</el-radio>
                <el-radio :label="'日'">日</el-radio>
              </el-radio-group>
              <el-date-picker
                v-model="ywblDate"
                :type="ywblWd==='年'?'year':(ywblWd==='月'?'month':'date')"
                style="width: 130px"
                size="mini"
                placeholder="选择日期"
                :value-format="ywblWd==='年'?'yyyy':(ywblWd==='月'?'yyyy-MM':'yyyy-MM-dd')"
              ></el-date-picker>
              <el-button size="mini" type="primary" style="margin-left: 10px" @click="GetTopTen">查找</el-button>
            </div>
          </div>
          <div class="chart-main" style="margin-top: 20px;height: 400px">
            <across-bar ref="acrossBar"></across-bar>
          </div>
        </div>
        <div class="content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>业务办理情况</span>
            <div class="bar_nav">
              <span>业务类型：</span>
              <el-select
                style="width: 220px;margin-right: 10px"
                size="mini"
                v-model="barValue"
                multiple
                filterable
                collapse-tags
                allow-create
                default-first-option
                placeholder="请选择业务类型"
              >
                <el-option
                  v-for="item in barOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span>时间：</span>
              <el-date-picker
                style="width: 180px"
                size="mini"
                value-format="yyyy-MM"
                v-model="barDate"
                type="monthrange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始月份"
                end-placeholder="结束月份"
              ></el-date-picker>
              <el-button
                size="mini"
                type="primary"
                style="margin-left: 10px"
                @click="GetBusinessSituation"
              >查找</el-button>
            </div>
          </div>
          <div class="chart-main" style="height: 300px" v-loading="barLoading">
            <bar-charts ref="barCharts" :typeVal="barOptions"></bar-charts>
          </div>
        </div>
        <div class="content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>渠道功能活跃分布</span>
            <div class="line_nav pie_nav">
              <span>渠道：</span>
              <el-select
                v-model="pieValue"
                clearable
                placeholder="请选择"
                style="width: 190px;margin-right: 10px"
                size="mini"
              >
                <el-option
                  v-for="item in pieOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span>时间：</span>
              <el-radio-group v-model="hyfbWd" @change="handleDate">
                <el-radio :label="'年'">年</el-radio>
                <el-radio :label="'月'">月</el-radio>
                <el-radio :label="'日'">日</el-radio>
              </el-radio-group>
              <el-date-picker
                v-model="hyfbDate"
                :type="hyfbWd==='年'?'year':(hyfbWd==='月'?'month':'date')"
                style="width: 130px"
                size="mini"
                placeholder="选择日期"
                :value-format="hyfbWd==='年'?'yyyy':(hyfbWd==='月'?'yyyy-MM':'yyyy-MM-dd')"
              ></el-date-picker>
              <el-button
                size="mini"
                type="primary"
                style="margin-left: 10px"
                @click="getFunctionalActiveDistribution"
              >查找</el-button>
            </div>
          </div>
          <div class="chart-main" style="height: 298px;" v-loading="pieLoading">
            <pie-charts ref="pieCharts"></pie-charts>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Interface from "@/base/mixin/channel-preference";
import acrossBar from "./template/acrossBar";
import barCharts from "./template/barCharts";
import lineCharts from "./template/lineCharts";
import pieCharts from "./template/pieCharts";
import mapTemp from "./template/mapTemp";
// import qs from 'qs';

export default {
  name: "analysis",
  mixins: [Interface],
  components: {
    acrossBar,
    barCharts,
    lineCharts,
    pieCharts,
    mapTemp
  },
  props: {},
  data() {
    return {
      //互联网渠道注册数分布
      mapLoading: false,
      options: [
        {
          value: "A渠道",
          label: "A渠道"
        },
        {
          value: "C渠道",
          label: "C渠道"
        },
        {
          value: "B渠道",
          label: "B渠道"
        }
      ],
      value: "全部",
      activeName: "first",
      areaArrayData: [], //城市所有渠道数据
      objTotal: {
        bindNum: 0, //总绑定数
        customerNum: 0, //总客户数
        registerNum: 0 //总注册数
      },
      bindNumArray: [], //总绑定数
      registerNumArray: [], //总注册数
      //渠道功能分类分布
      lineDate: [],
      lineOptions: [],
      lineValue: ["全部"],
      lineLoading: false,
      //业务办理情况
      barDate: [],
      barOptions: [],
      barValue: ["全部"],
      barLoading: false,
      //渠道功能活跃分布
      pieLoading: false,
      pieOptions: [],
      pieValue: "微信",
      hyfbDate: "",
      hyfbWd: "年",
      //互联网渠道办理业务top10
      unitValue: [],
      unitOptions: [],
      ywblWd: "年",
      ywblDate: "",
      cityArr: [
        "广州市",
        "佛山市",
        "东莞市",
        "中山市",
        "江门市",
        "珠海市",
        "惠州市",
        "汕头市",
        "清远市",
        "肇庆市",
        "韶关市",
        "梅州市",
        "湛江市",
        "茂名市",
        "揭阳市",
        "潮州市",
        "云浮市",
        "河源市",
        "阳江市",
        "汕尾市",
        "深圳市"
      ]
    };
  },
  mounted() {
    this.ywblDate = this.getDate("year").toString();
    this.hyfbDate = this.getDate("year").toString();
    this.$nextTick(() => {
      //渠道功能分类分布
      this.GetFunctionChannel(); //获取渠道下拉框
      this.getFunctionDistribution(); //获取渠道数据
      //业务办理情况接口
      this.GetBusinessType(); //获取业务类别下拉
      this.GetBusinessSituation(); //根据业务类别和时间段获取数据
      //互联网渠道注册数分布
      this.GetRegisterChannel(); //渠道条件下拉框列表数据获取功能
      this.GetDetail(); //按渠道获取各地市的数据功能
      //渠道功能活跃分布
      this.getFunctionalActiveDistribution();
      //互联网渠道办理业务TOP10排名
      this.GetUnit();
    });
  },
  methods: {
    dateChange() {
      this.ywblDate = "";
    },
    handleClick(tab, event) {
      window.console.log(tab, event);
    },
    handleDate() {
      this.hyfbDate = "";
      window.console.log(this.hyfbWd, "pp");
    },
    //获取当前年月日
    getDate(type) {
      var nowDate = new Date();
      var year = nowDate.getFullYear();
      var month =
        nowDate.getMonth() + 1 < 10
          ? "0" + (nowDate.getMonth() + 1)
          : nowDate.getMonth() + 1;
      var day =
        nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
      if (type === "year") {
        return year;
      }
      if (type === "month") {
        return year + "-" + month;
      }
      if (type === "date") {
        return year + "-" + month + "-" + day;
      }
    },
    /**
     *
     * 互联网渠道注册数分布
     * **/
    //渠道条件下拉框列表数据获取功能
    GetRegisterChannel() {
      this.getRegisterChannel({}).then(res => {
        this.options = res;
        this.options.unshift({
          value: "全部",
          label: "全部"
        });
      });
    },
    //按渠道获取各地市的数据功能
    GetDetail() {
      this.mapLoading = true;
      this.areaTypeChange();
      // this.getDetail({}).then(res => {
      //   this.mapLoading = false;
      //   this.areaArrayData = res
      //   this.areaTypeChange()//默认地图查全部
      //   window.console.log(res)
      // })
    },
    areaTypeChange() {
      // this.$refs.mapTemp.setMap(this.areaArrayData, this.value)
      this.GetNumber();
      this.GetTotalNumber();
    },

    //按渠道获取各地市的数据功能
    GetNumber() {
      (this.objTotal = {
        bindNum: 0, //总绑定数
        customerNum: 0, //总客户数
        registerNum: 0 //总注册数
      }),
        this.getNumber({}).then(res => {
          for (let key in res) {
            if (key === this.value) {
              this.objTotal.customerNum = res[key].customerNum || 0;
              this.objTotal.bindNum = res[key].bindNum || 0;
              this.objTotal.registerNum = res[key].registerNum || 0;
            }
          }
        });
    },
    //按渠道获取各地市的数据功能
    GetTotalNumber() {
      this.bindNumArray = []; //总绑定数
      this.registerNumArray = []; //总注册数
      this.getTotalNumber({}).then(res => {
        window.console.log(res, "jjjjjj");
        this.mapLoading = false;
        this.areaArrayData = res;
        this.$refs.mapTemp.setMap(this.areaArrayData, this.value);
        window.console.log(this.value, "this.valuethis.value");
        for (let key in res) {
          if (key === this.value) {
            for (let i = 0; i < res[key].length; i++) {
              if (this.value === "支付宝" || this.value === "微信") {
                (this.activeName = "second"),
                  this.bindNumArray.push({
                    name: res[key][i].name,
                    bindNum: res[key][i].bindNum
                  });
                this.registerNumArray.push({
                  name: res[key][i].name,
                  registerNum: res[key][i].registerNum
                });
              } else if (this.value === "全部") {
                this.bindNumArray.push({
                  name: res[key][i].name,
                  bindNum: res[key][i].bindNum
                });
                this.registerNumArray.push({
                  name: res[key][i].name,
                  registerNum: res[key][i].registerNum
                });
              } else {
                this.activeName = "first";
                this.bindNumArray.push({
                  name: res[key][i].name,
                  bindNum: res[key][i].bindNum
                });
                this.registerNumArray.push({
                  name: res[key][i].name,
                  registerNum: res[key][i].registerNum
                });
              }
            }
          }
        }
        window.console.log(this.registerNumArray, "registerNumArray");
        const arr = [];
        const arrBing = [];
        this.cityArr.forEach(item => {
          arr.push(
            this.registerNumArray.find(ele => {
              return ele.name === item;
            })
          );
          arrBing.push(
            this.bindNumArray.find(ele => {
              return ele.name === item;
            })
          );
        });
        this.registerNumArray = arr;
        this.bindNumArray = arrBing;
        this.$forceUpdate();
      });
    },
    /**
     *渠道功能分类分布接口
     * **/
    //获取渠道下拉框
    GetFunctionChannel() {
      this.getFunctionChannel({}).then(res => {
        this.lineOptions = res;
        this.lineOptions.unshift({
          value: "全部",
          label: "全部"
        });
        this.pieOptions = JSON.parse(JSON.stringify(this.lineOptions));

        window.console.log(res);
      });
    },
    //根据渠道、时间段获取数据
    getFunctionDistribution() {
      this.lineLoading = true;
      this.lineDate === undefined || this.lineDate === null
        ? (this.lineDate = [])
        : this.lineDate; //点击删除已选日期时，绑定日期变为null
      this.functionDistribution({
        functionChannel: this.lineValue,
        startTime: this.lineDate[0] || "",
        endTime: this.lineDate[1] || ""
      }).then(res => {
        let legendData = [];
        let dataX = [];
        let series = []; //[{name: '邮件营销',type: 'line',stack: '总量',smooth: true,data: [120, 132, 101, 134, 90, 230, 210]}]
        for (let key in res) {
          legendData.push(key);
          series.push({
            name: key,
            type: "line",
            stack: "总量",
            smooth: true,
            data: []
          });
        }

        //获取dataX
        for (let s = 0; s < res[legendData[0]].length; s++) {
          window.console.log();
          dataX.push(res[legendData[0]][s].functionType);
        }
        //获取series
        let colorArray = [
          "#3c86e0",
          "#e0c53c",
          "#14e5cd",
          "#c96f20",
          "#68b45b",
          "#722dc1",
          "#87dbc7",
          "#0812d2"
        ];
        for (let key in res) {
          for (let i = 0; i < series.length; i++) {
            series[i].color = colorArray[i];
            if (series[i].name === key) {
              for (let j = 0; j < res[key].length; j++) {
                series[i].data.push(res[key][j].num);
              }
            }
          }
        }
        this.lineLoading = false;
        this.$refs.lineCharts.init(legendData, series, dataX);
        window.console.log(legendData, "ooo");
        window.console.log(series, "series");
        window.console.log(dataX, "dataX");
      });
    },

    /**
     *
     *渠道功能活跃分布
     * **/
    //根据渠道、时间和时间维度获取数据
    getFunctionalActiveDistribution() {
      this.pieLoading = true;
      this.functionalActiveDistribution({
        dimension: this.hyfbWd,
        time: this.hyfbDate,
        channel: this.pieValue
      }).then(res => {
        let legendData = [];
        let seriesData = [];
        res.data.forEach(item => {
          legendData.push(item.functionType);
          seriesData.push({
            name: item.functionType,
            value: item.proportion
          });
        });
        this.$refs.pieCharts.init(
          legendData,
          seriesData,
          this.pieOptions,
          this.pieValue,
          this.hyfbWd,
          this.hyfbDate
        );
        window.console.log(res, "rr");
        this.pieLoading = false;
      });
    },

    /**
     * 业务办理情况接口
     * **/

    //获取业务类别下拉
    GetBusinessType() {
      this.getBusinessType({}).then(res => {
        this.barOptions = res;
        this.barOptions.unshift({
          value: "全部",
          label: "全部"
        });
      });
    },
    //根据业务类别和时间段获取数据
    GetBusinessSituation() {
      this.barLoading = true;
      this.barDate === undefined || this.barDate === null
        ? (this.barDate = [])
        : this.barDate;
      window.console.log(this.barDate, "ooo");
      this.businessSituation({
        businessType: this.barValue,
        startTime: this.barDate[0] || "",
        endTime: this.barDate[1] || ""
      }).then(res => {
        this.barLoading = false;
        let dataX = [];
        let series = [];
        for (let i = 0; i < res.length; i++) {
          dataX.push(res[i].channel);
          series.push(res[i].num);
        }
        this.$nextTick(() => {
          this.$refs.barCharts.init(dataX, series, this.barValue, this.barDate); //.... 时间-业务类别
        });
      });
    },
    /**
     *互联网渠道办理业务TOP10排名
     * **/
    //获取业务类别下拉
    GetUnit() {
      this.getUnit({}).then(res => {
        this.unitOptions = res;
        this.GetTopTen();
      });
    },
    //获取业务类别下拉
    GetTopTen() {
      this.getTopTen({
        dimension: this.ywblWd,
        time: this.ywblDate,
        cities: this.unitValue
      }).then(res => {
        let array = [["score", "amount", "product"]];
        let data = res.sort((a, b) => {
          return a.sl - b.sl;
        });
        data.forEach((item, index) => {
          if (index <= 9) {
            array.push([item.sl, 0, item.ywlb]);
            console.log(array, "array");
          } else {
            return;
          }
        });
        this.$refs.acrossBar.init(
          array,
          this.unitOptions,
          this.unitValue,
          this.ywblWd,
          this.ywblDate
        ); //图表数据、单位list、单位、维度、日期
      });
    }
  }
};
</script>

<style scoped lang="scss">
.analysis {
  ::v-deep .el-radio {
    margin-right: 6px;
    .el-radio__input.is-checked .el-radio__inner {
      background: #409eff !important;
    }
  }
  .content-border {
    background: #fff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
    margin-bottom: 10px;

    .content-title {
      display: flex;
      align-items: center;
      height: 40px;
      line-height: 30px;
      padding: 5px 0px 0px 5px;
      font-weight: bold;
      color: #1a84f4;
      position: relative;
      margin-bottom: 10px;

      .block {
        display: inline-block;
        width: 8px;
        height: 16px;
        margin: 0 10px;
        background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
      }

      .line_nav {
        position: absolute;
        right: 4px;
        top: 30px;
      }
      .yw_nav {
        top: 30px;
      }
      .pie_nav {
        top: 30px;
      }
      .bar_nav {
        position: absolute;
        right: 4px;
        top: 30px;
      }
    }

